import React from 'react';

import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const Education = () => {
  return (
    <div className={styles.container}>
      <h1 className={jcn(styles.headline, 'textColorGeneral')}>
        Education
      </h1>
      <div className={jcn(styles.horizontalLine, 'backgroundColorGeneral')} />
      <div className={styles.educationInfoContainer}>
        <div className={styles.schoolName}>
          Kharkiv National University of Radio Electronics
        </div>
        <div className={styles.faculty}>
          Faculty of Computer Engineering and Control
        </div>
        <div className={styles.degreeDesc}>
          Bachelor Degree 2015-2019
        </div>
      </div>
    </div>
  );
};


export default Education;
