import React from 'react';
import PropTypes from 'prop-types';

import styles from './styles.module.sass';


const SkillItem = ({ name }) => {
  return (
    <li key={name} className={styles.listItem}>
      {name}
    </li>
  );
};


SkillItem.propTypes = {
  name: PropTypes.string,
};

export default SkillItem;
