import React from 'react';

import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const SKILLS_LEVELS = {
  1: 'Beginner',
  2: 'Basic',
  3: 'Intermediate',
  4: 'Advanced',
  5: 'Expert'
}

const SKILLS = [
  { name: 'JavaScript', level: 5 },
  { name: 'React JS', level: 4 },
  { name: 'React Native', level: 3 },
  { name: 'CSS', level: 4 },
  { name: 'HTML', level: 3 },
  // { name: 'SASS', level: 3 },
  { name: 'NodeJS', level: 3 }
];


const getProgressItems = skill => {
  const arrayOfItems = [];
  for (let i = 0; i < 5; i++) {
    arrayOfItems.push(
      skill.level >= i + 1
        ? <div
          className={jcn(
            styles.progressItemFull, 'backgroundColorGeneral', 'borderColorGeneral'
          )} key={`${skill.name}-${i}`}
        />
        : <div
          className={jcn(
            styles.progressItemEmpty, 'borderColorGeneral'
          )} key={`${skill.name}-${i}`}
        />
    );
  }

  return arrayOfItems;
}


const Skills = () => {
  return (
    <div className={styles.container}>
      <h1 className={jcn(styles.headline, 'textColorGeneral')}>
        Skills
      </h1>
      <div className={jcn(styles.horizontalLine, 'backgroundColorGeneral')} />
      <div className={styles.skillsContainer}>
        {
          SKILLS.map(skill => <div className={styles.skillContainer} key={skill.name}>
            <div className={styles.skillDescription}>
              <h3 className={styles.skillName}>{skill.name}</h3>
              <div className={styles.skillLevel}>{SKILLS_LEVELS[skill.level]}</div>
            </div>

            <div className={styles.skillProgressBar}>
              {getProgressItems(skill)}
            </div>
          </div>)
        }
      </div>
    </div>
  );
};


export default Skills;
