import React from 'react';

import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const OPEN_SOURCE_PROJECTS = [
  {
    name: 'REACT-STRING-CLAMP',
    description: 'Text clamping ReactJS-component.',
    link: 'https://www.npmjs.com/package/react-string-clamp'
  }
];


const OpenSource = () => {
  return (
    <div className={styles.container}>
      <h1 className={jcn(styles.headline, 'textColorGeneral')}>
        Open Source
      </h1>
      <div className={jcn(styles.horizontalLine, 'backgroundColorGeneral')} />
      <div className={styles.itemsContainer}>
        {
          OPEN_SOURCE_PROJECTS.map(OSProject => {
            return <div className={styles.projectContainer} key={OSProject.name}>
              <a
                href={OSProject.link} target='_blank'
                className={styles.projectLink} rel='noopener noreferrer'
              >
                <h3 className={styles.projectTitle}>{OSProject.name}</h3>
              </a>
              <div className={styles.projectDesc}>{OSProject.description}</div>
            </div>;
          })
        }
      </div>
    </div>
  );
};


export default OpenSource;
