import React from 'react';

import SkillItem from './SkillItem';
import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const SKILLS = [
  'GIT', 'GitHub', 'GitLab', 'JIRA', 'SASS', 'Axios', 'Redux',
  'ESLint', 'MySQL'
];


const AdditionalSkills = () => {
  const firstColumnIndexes = [];
  const secondColumnIndexes = [];

  for (const indx in SKILLS) {
    if (indx % 2) {
      secondColumnIndexes.push(indx);
    } else {
      firstColumnIndexes.push(indx);
    }
  }

  return (
    <div className={styles.container}>
      <h1 className={jcn(styles.headline, 'textColorGeneral')}>
        Additional Skills
      </h1>
      <div className={jcn(styles.horizontalLine, 'backgroundColorGeneral')} />
      <div className={styles.skillsContainer}>
        <div className={styles.listColumnContainer}>
          <ul className={styles.listContainer}>
            {
              firstColumnIndexes.map(
                skillIndx => <SkillItem name={SKILLS[skillIndx]} />
              )
            }
          </ul>
        </div>
        <div className={styles.listColumnContainer}>
          <ul className={styles.listContainer}>
            {
              secondColumnIndexes.map(
                skillIndx => <SkillItem name={SKILLS[skillIndx]} />
              )
            }
          </ul>
        </div>
      </div>
    </div>
  );
};


export default AdditionalSkills;
