import React from 'react';

import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const LANGUAGE_LEVELS = {
  1: 'Beginner',
  2: 'Pre-Intermediate',
  3: 'Intermediate',
  4: 'Upper Intermediate',
  5: 'Fluently',
  6: 'Native'
}

const LANGUAGES = [
  { name: 'English', level: 2 },
  { name: 'Russian', level: 6 },
  { name: 'Ukrainian', level: 6 }
];


const getProgressItems = skill => {
  const arrayOfItems = [];
  for (let i = 0; i < Object.keys(LANGUAGE_LEVELS).length; i++) {
    arrayOfItems.push(
      skill.level >= i + 1
        ? <div
          className={jcn(
            styles.progressItemFull, 'backgroundColorGeneral', 'borderColorGeneral'
          )} key={`${skill.name}-${i}`}
        />
        : <div
          className={jcn(
            styles.progressItemEmpty, 'borderColorGeneral'
          )} key={`${skill.name}-${i}`}
        />
    );
  }

  return arrayOfItems;
}


const Languages = () => {
  return (
    <div className={styles.container}>
      <h1 className={jcn(styles.headline, 'textColorGeneral')}>
        Languages
      </h1>
      <div className={jcn(styles.horizontalLine, 'backgroundColorGeneral')} />
      <div className={styles.skillsContainer}>
        {
          LANGUAGES.map(skill => <div className={styles.skillContainer} key={skill.name}>
            <div className={styles.skillDescription}>
              <h3 className={styles.skillName}>{skill.name}</h3>
              <div className={styles.skillLevel}>{LANGUAGE_LEVELS[skill.level]}</div>
            </div>

            <div className={styles.skillProgressBar}>
              {getProgressItems(skill)}
            </div>
          </div>)
        }
      </div>
    </div>
  );
};


export default Languages;
