import React from 'react';

import {
  FaPhone, FaEnvelope, FaLinkedin, FaTelegram, FaGithub, FaNpm, FaMapMarked
} from 'react-icons/fa';
import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const Header = () => {
  return (
    <div className={jcn(styles.container, 'backgroundColorGeneral')}>

      <img
        className={styles.avatar}
        src={require('./avatar.jpg')} alt="avatar"
      />

      <div className={styles.infoContainer}>
        <div className={styles.briefDescriptionContainer}>
          <div className={styles.whoamiContainer}>
            <h1 className={styles.name}>Andrey Khar'kovets</h1>
            <h2 className={styles.position}>Front-End developer</h2>
          </div>

          <div className={styles.contactsContainer}>
            <div className={styles.contactRow}>
              <FaPhone className={styles.contactIcon} /> (hidden in a public CV)
            </div>
            <div className={styles.contactRow}>
              <FaEnvelope className={styles.contactIcon} /> a.harkovets@gmail.com
            </div>
            <div className={styles.contactRow}>
              <FaMapMarked className={styles.contactIcon} /> Ukraine, Kharkiv
            </div>
          </div>
        </div>
        <div className={styles.antiAdblockSocialsContainerClass} >
          <a href='https://t.me/harkovets' className={styles.antiAdblockSocialLink}>
            <FaTelegram className={styles.socialIcon} /> t.me/harkovets
          </a>
          <a href='https://github.com/a-harkovets' className={styles.antiAdblockSocialLink}>
            <FaGithub className={styles.socialIcon} /> github.com/a-harkovets
          </a>
          <a href='https://www.npmjs.com/~a.kharkovets' className={styles.antiAdblockSocialLink}>
            <FaNpm className={styles.socialIcon} /> npmjs.com/~a.kharkovets 
          </a>
          <a
            href='https://www.linkedin.com/in/harkovets/' className={styles.antiAdblockSocialLink}>
            <FaLinkedin className={styles.socialIcon} /> linkedin.com/in/harkovets
          </a>
        </div>
      </div>
    </div>
  );
};


export default Header;
