import React from 'react';

import jcn from '../Utils/jcn';

import styles from './styles.module.sass';


const EXPERIANCES = [
  {
    from: '05.2018', to: null,
    desc: 'Speedflow Communications Ltd. React JS/Native & Node JS developer.'
  },
  { from: '03.2018', to: '05.2018', desc: 'Remote work. Part time.' }
];

const WorkExperiences = () => {
  return (
    <div className={styles.container}>
      <h1 className={jcn(styles.headline, 'textColorGeneral')}>
        Experience
      </h1>
      <div className={jcn(styles.horizontalLine, 'backgroundColorGeneral')} />
      <div className={styles.experiencesContainer}>
        {
          EXPERIANCES.map(experience => <div
            className={styles.experienceItem}
            key={experience.desc}
          >
            <div className={styles.experienceItemDate}>
              <div className={styles.experienceItemFrom}>{experience.from}</div>
              <div className={styles.experienceItemTo}>{experience.to === null ? 'CURRENT' : experience.to}</div>
            </div>
            <div className={styles.experienceItemDesc}>
              {experience.desc}
            </div>
          </div>)
        }
      </div>
    </div>
  );
};


export default WorkExperiences;
